.content-error {
  margin: 0 auto;
  max-width: 960px;
}
.content-error:first-child {
  margin-top: 24px;
}
.error_area {
  margin-left: 24px;
  color: #ff0000;
}
.error_area .error_msg_txt {
  font-size: 16px;
}
.error_area .error_msg_txt::before {
  content: '●';
  color: #ff0000;
  font-weight: bold;
  font-size: 12px;
  margin-right: 5px;
}
